/**
 * 计算低价、成本、报价
 * 
 * @param area 面积
 * @param year 年限
 * @param sales 月销售额
 * @param typeC 品类系数
 * @param cityC 城市系数
 */
export function calcPrice(area: number, year: number, sales: number, typeC: number, cityC: number) {
    let basePrice = 0 // 标准报价
    let floorPrice = 0 // 成交底价==成交底价（标准报价）*品类系数*城市系数
    let costPrice = 0 // 成交价=成交底价*成交价系数
    let offerPrice = 0 // 报价=成交价*报价系数
    let costC = 1 // 成交价系数
    let offerC = 1 // 报价系数

    if (area < 50) {
        return false
    }
    if (area < 100) {
        basePrice = (area / 60 * 200 + 150) * (year / 3 * 0.15 + 0.85) * (sales / area / 1700 * 0.1 + 0.9)
        costC = 1.18
        offerC = 1.25
    } else if (area < 200) {
        basePrice = (area / 170 * 300 + 300) * (year / 3 * 0.15 + 0.85) * (sales / area / 1400 * 0.1 + 0.9)
        costC = 1.15
        offerC = 1.2
    } else if (area < 400) {
        basePrice = (area / 290 * 450 + 350) * (year / 3 * 0.15 + 0.85) * (sales / area / 1300 * 0.1 + 0.9)
        costC = 1.15
        offerC = 1.2
    } else if (area < 800) {
        basePrice = (area / 550 * 800 + 350) * (year / 3 * 0.15 + 0.85) * (sales / area / 1100 * 0.1 + 0.9)
        costC = 1.12
        offerC = 1.15
    } else if (area < 1400) {
        basePrice = (area / 1100 * 1300 + 500) * (year / 3 * 0.15 + 0.85) * (sales / area / 1000 * 0.1 + 0.9)
        costC = 1.1
        offerC = 1.12
    } else if (area < 2300) {
        basePrice = (area / 1800 * 1500 + 1000) * (year / 3 * 0.15 + 0.85) * (sales / area / 900 * 0.1 + 0.9)
        costC = 1.1
        offerC = 1.12
    } else if (area <= 3500) {
        basePrice = (area / 2800 * 2300 + 1300) * (year / 4 * 0.15 + 0.85) * (sales / area / 900 * 0.1 + 0.9)
        costC = 1.1
        offerC = 1.12
    }
    if (area >= 400 && cityC === 1.1) {
        cityC = 1.05
    }
    if (area >= 400 && cityC === 1.35) {
        cityC = 1.3
    }
    floorPrice = basePrice * typeC * cityC
    costPrice = floorPrice * costC
    offerPrice = costPrice * offerC
    // console.log(area, year, sales, typeC, cityC)
    // console.log('calc result: ', basePrice, floorPrice, costPrice, offerPrice)
    return {
        floor: floorPrice.toFixed(0),
        cost: costPrice.toFixed(0),
        offer: offerPrice.toFixed(0)
    }
}

/** 
 * param 将要转为URL参数字符串的对象 
 * encode true/false 是否进行URL编码,默认为true 
 *  
 * return URL参数字符串 
 */
export function urlEncode(param: any, encode = true) {
    if (param == null) return '';
    const paramAry = [];
    for (const i in param) {
        paramAry.push(i + '=' + (encode ? encodeURIComponent(param[i]) : param[i]))
    }
    return paramAry.join('&');
}

export function isNumber(obj: any) {
    return typeof obj === 'number' && !isNaN(obj)
}
