import { httpGet } from "./http"

const User = {
    openid: '',
    getOpenid() {
        // if (process.env.NODE_ENV === 'development') {
        //     return 'oPKdSwC58K57Mskin6hDQUJdSLqs'
        // }
        // return localStorage.getItem('openid')
        return this.openid
    },
    async login() { // 静默授权
        if (!!this.getOpenid()) {
            return this.getUserInfo(this.getOpenid())
        }
        const code = this.getQueryVariable('code') // 截取路径中的code，如果没有就去微信授权，如果已经获取到了就直接传code给后台获取openId
        const local = window.location.href
        if (!!!code) {
            const appId = 'wx364a8470a58b03ed'
            const redirect_uri = encodeURIComponent(local)
            window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appId}&redirect_uri=${redirect_uri}&response_type=code&scope=snsapi_base&state=#wechat_redirect`
        } else {
            const redirectUrl = this.getLocationParam('url')
            const code = this.getLocationParam('code')
            if (redirectUrl) {
                window.location = redirectUrl + '?code=' + code
                return
            }
            try {
                const { openid } = await httpGet('/getopenid', { code: code })
                this.openid = openid
                return this.getUserInfo(openid)
            } catch (error) {
                window.location = '/'
            }
        }
    },
    getLocationParam(name) {
        const reg = new RegExp('(^|&|/?)' + name + '=([^&]*)(&|$)')
        const r = window.location.search.match(reg);
        if (r !== null) return decodeURIComponent(r[2])
        return null;
    },
    async getUserInfo(openid) { // 通过code获取 openId等用户信息
        return new Promise(async (resolve, reject) => {
            try {
                const res = await httpGet('/userinfo', { openid: openid })
                return resolve(res)
            }
            catch (error) {
                return reject(error)
            }
        })
    },
    // 获取当前url参数
    getQueryVariable(variable) {
        var query = window.location.search.substring(1);
        var vars = query.split("&");
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split("=");
            if (pair[0] === variable) { return pair[1]; }
        }
        return (false);
    }
}
export default User